.cat-info {
  align-items: flex-start;
  gap: 20px;
  margin-top: 12px;
  display: flex;
}

.cat-img {
  max-width: 300px;
  height: auto;
}

.text-bold {
  font-weight: bold;
}

.cat-text {
  width: 40%;
  vertical-align: top;
}

.cat-name {
  margin: 0;
}

.loader-container, .error {
  display: none;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #000;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin-top: 10px;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 24px;
  height: 24px;
  transform-origin: center;
  border: 3px solid #ff3d00;
  border-top-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: auto;
  animation: rotationBack .5s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

